.synset-selector__synset-row {
    display: -ms-flexbox;
    display: flex;

    padding: 0 0.4em 0 0.4em;
    margin-bottom: 0.33em;

    background-color: bisque;
    border-radius: 15px;
}

.synset-selector__synset-row:hover,
.synset-selector__synset-row--selected {
    background-color: #d0baa0;
    border: 1px whitesmoke solid;
}

.synset-selector__synset-label {
    font-style: italic;
}

.synset-selector__comment-count {
    text-align: center;
    background-color: darkorange;

    border-radius: 1.2em;
    padding: 0.16em 1em 0.16em 1em;
}

@media only screen and (min-width: 600px){
    .synset-selector__synset-row {
        -ms-flex-flow: row nowrap;
            flex-flow: row nowrap;
    }

    .synset-selector__synset-gloss {
        -ms-flex-order: 1;
            order: 1;
        -ms-flex-positive: 8;
            flex-grow: 8;
        -ms-flex-preferred-size: 0;
            flex-basis: 0;

        padding-top: 0.75em;
        padding-bottom: 0.75em;
    }

    .synset-selector__comment-count-container {
        -ms-flex-order: 2;
            order: 2;
        -ms-flex-positive: 2;
            flex-grow: 2;
        -ms-flex-preferred-size: 0;
            flex-basis: 0;
        -ms-flex-item-align: center;
            align-self: center;

        display: -ms-flexbox;

        display: flex;
        -ms-flex-pack: end;
            justify-content: flex-end;
        margin-left: 0.25em;
    }

    .synset-selector__comment-count {
        font-size: 0.8em;
    }
}

@media only screen and (max-width: 600px){
    .synset-selector__synset-row {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-flow: column nowrap;
            flex-flow: column nowrap;
        -ms-flex-align: center;
            align-items: center;
    }

    .synset-selector__synset-gloss {
        -ms-flex-order: 1;
            order: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
        text-align: center;

        margin-top: 0.2em;
        margin-bottom: 0.7em;
    }

    .synset-selector__comment-count-container {
        -ms-flex-order: 2;
            order: 2;
        margin-bottom: 0.7em;
    }
}




